.btn {
    padding: 26px;
    font-weight: normal;
    font-size: 32px;
    line-height: 1rem;
    width: 69px;
    height: 72px;
    border-radius: 24px;
    text-align: center;
    transition: background 0.3s ease-out;
    width: 100%;

    &:hover {
        background: $grayColor;
    }
}

.calc {
  padding: 25px;
  @include centred;

  &__container {
      padding: 25px;
  }

  &__main {
      display: grid;
      grid-template: repeat(2, auto) / repeat(1, auto);
      gap: 15px;
  }
}

.calc-reset {
    grid-column: 1 / 4;
    display: grid;
    grid-template: 1fr / repeat(3, 1fr);
    gap: 15px;
    &__all {
        background: $darkGrayColor;
        grid-column: 1 / 3;
        grid-row: 1;
        width: 100%;
    }
    &__some {
        background: $darkGrayColor;
        font-size: 24px;
        padding: 26px 12px;
        grid-row: 1;
        grid-column: 3;
    }
}

.calc-result {
  font-weight: 300;
  font-size: 96px;
  line-height: 96px;
  margin-bottom: 16px;
  text-align: right;
}

.calc-numbers {
    grid-column: 1 / 4;
    grid-row: 2 / auto;
    display: grid;
    grid-template: repeat(3, 1fr) / repeat(3, 1fr);
    gap: 15px;
  &__item {
    background: $darkGrayColor;
    &_zero {
        width: 100%;
        grid-row: auto;
        grid-column: 1 / 4;
    }
  }
}

.calc-actions {
    display: grid;
    gap: 15px;
    grid-column: 4;
    grid-row: 1 / 3;
    &__item {
        background: $blueColor;
        &:hover {
            background: $darkblueColor;
        }
    }
}
