html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Work Sans', 'Roboto', sans-serif;
  font-display: swap;
  color: $whiteColor;
  background: #17171C;
}


img {
  max-width: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  color: inherit;
}


a,
button,
input,
textarea {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5 {
  display: block;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}


.site-container {
  position: relative;
  overflow-x: hidden;
}
